<template>
    <div class="black-bg" v-if="show">
      <div class="white-bg"> 
        <button class="close" @click="closeP">X</button> 
        <div class = 'gallery-container'>
            <div v-for="(photo, index) in photos" :key="index" class="image-container">
                <img :src="photo" :alt="'knps_photo' + index" @click = 'downloadPhoto(photo,index)'>
            </div>
        </div>
      </div>
    </div>
</template>

<script>
import EventBus from '@/EventBus.js';

export default {
    name: 'AppGallery',
    mounted() {
        EventBus.$on('add-photo',this.addPhoto)
    },
    props: {
        show: {
                type: Boolean,
                required: true,
            },
    },
    data(){
        return {
            photos: [],
        };
    },
    methods: {
        addPhoto(photoUrl) {
            this.photos.push(photoUrl)
        },
        closeP(){
            this.$emit('close');
        },
        downloadPhoto(photo,index) {
            const a = document.createElement('a');
            a.href = photo;
            a.download = `knps_${index}`;
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
        },
    },
    beforeDestroy() {
    EventBus.$off('add-photo', this.photoUrl);
    this.photos.forEach(URL.revokeObjectURL);
    },
};
</script>
<style scoped>
.black-bg{
    width: 100%; height: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: fixed; 
    top: 0;
    left: 0;
    padding: 20px;
    z-index: 1500;
    display: flex;
    align-items: center;
    justify-content: center;
}
.white-bg{
    width: 90%; height: 90%;
    background-color: white;
    /* position: relative; */
    border-radius: 8px;
    position: fixed; 
    padding: 20px;
    overflow-y: auto;
      }
.gallery-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px; /* Adjust the gap as needed */
}

.image-container {
    width: 150px; /* Set fixed width */
    height: 150px; /* Set fixed height */
    overflow: hidden;
    position: relative;
    border: 1px solid gray; /* 검은색 테두리 추가 */
}

.image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* This will cover the area without stretching the image */
    cursor: pointer; /* Indicates that the image is clickable */
}
.close{

    display: flex;
    width: 5%;
    padding: 10px 10px;
    margin-top: 5px;
    background-color: #ccc;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-left: auto; /* 왼쪽 마진을 오토로 설정해서 오른쪽으로 밀어냄 */
    justify-content: center;

}
</style>